*{box-sizing:border-box;margin:0;padding:0}body{background:#13202c;background:linear-gradient(90deg, #13202c 0%, #1f2f3d 35%, #111c27 100%);font-family:'Helvetica Neue', 'Helvetica', 'Arial', serif;text-transform:lowercase;font-weight:700;line-height:1;color:#FFF}h1,h2,h3,h4,h5,h6{font-weight:normal}h1{font-size:3.2rem}a{text-decoration:none;color:inherit}.container{width:90vw;margin:0 auto}header{height:130px;padding:40px 0}header h1{position:relative;margin-right:100px;color:rgba(255,255,255,0.8)}header h1 span{position:relative;z-index:1}header .container{display:flex;align-items:center}header nav{display:inline-flex}header nav:last-child{margin-left:auto}header nav a{margin-right:2em;font-size:1.4rem;transition:opacity 0.2s ease-in-out;opacity:0.8}header nav a:hover,header nav a:focus{opacity:1}header nav a:last-child{margin-right:0}header nav a.filtered{opacity:0.5}.btn{border:none;outline:none;background:#FFF;color:#5FCC9F;cursor:pointer}.btn-circle{position:relative;width:70px;height:70px;padding:27px 28px;border-radius:50%}.btn-circle svg{position:relative;z-index:1}.btn-circle:before,.btn-circle:after{display:block;opacity:0;position:absolute;border-radius:50%;content:'';transition:opacity 0.25s ease-in-out}.btn-circle:hover:before,.btn-circle:hover:after,.btn-circle:focus:before,.btn-circle:focus:after{opacity:1}.btn-circle:before{top:-5px;left:-5px;width:80px;height:80px;background:rgba(255,255,255,0.5)}.btn-circle:after{top:-10px;left:-10px;width:90px;height:90px;background:rgba(255,255,255,0.15)}.soundcloud-widget{display:none}.player{padding:60px 0}.player .container{display:flex;align-items:center}.btn-play{padding:22px 24px;color:#1A2936}.player__content{flex:1}.track-title{font-size:2.4rem}.track-artwork{width:250px;height:250px;margin-right:30px}.player__controls{display:flex;align-items:center}.player__controls .btn{margin-right:30px}.icon-pause{display:none;margin-left:-1px;margin-right:1px}.player__controls-right{display:inline-flex;align-items:center;position:relative;margin-left:auto}.player__controls-right>a{display:flex;align-items:center;margin-right:25px}.player__controls-right>a:last-child{margin-right:0}.waveform{display:flex;align-items:center;height:40px;margin-top:35px}.waveform .bar{width:2px;margin-right:3px;background:#FFF;transition:background 0.5s ease-in-out}.sharer{display:none;flex-wrap:wrap;justify-content:center;position:absolute;top:140%;right:10px;width:100vw;max-width:450px;padding:25px;border-radius:3px;background:#FFF}.sharer a{width:calc(50% - 10px);margin:0 20px 20px 0;text-decoration:none;color:#FFF}.sharer a:nth-child(even){margin-right:0}.sharer a:nth-child(3),.sharer a:nth-child(4){margin-bottom:0}.btn-share{border-radius:3px;transition:25ms ease-out;padding:0.5em 0.75em}.btn-share__icon{display:inline-block}.btn-share__icon svg{width:1em;height:1em;margin-right:0.4em;vertical-align:top}.btn-share__icon{fill:#fff;stroke:none}.btn-share--twitter{background-color:#55acee}.btn-share--twitter:hover,.btn-share--twitter:focus{background-color:#2795e9}.btn-share--facebook{background-color:#3b5998}.btn-share--facebook:hover,.btn-share--facebook:focus{background-color:#2d4373}.btn-share--google{background-color:#dd4b39}.btn-share--google:hover,.btn-share--google:focus{background-color:#c23321}.btn-share--email{background-color:#777}.btn-share--email:hover,.btn-share--email:focus{background-color:#5e5e5e}.the-congregation{display:flex;align-items:center;justify-content:center;min-height:100vh;margin-top:-130px;padding:140px 0;background-color:#143329;background:url(/img/the-congregation.jpg) no-repeat;background-size:cover;background-position:50% 20%;color:#fff}.the-congregation .track-title{text-shadow:1px 1px 4px rgba(0,0,0,0.1)}.the-congregation .btn-play{color:#143329}
